require('normalize.css/normalize.css');
require('styles/App.scss');



import React from 'react';
import ReactDOM from 'react-dom';


// let yeomanImage = require('../images/yeoman.png');

//
let imageDatas = require('../data/imageDatas.json');

//URL
imageDatas = (function genImageURL(imageDatasArr){
	for(var i = 0; i < imageDatasArr.length; i++){
		var singleImageData =  imageDatasArr[i];

		singleImageData.imageURL = require('../images/' + singleImageData.fileName);
		imageDatasArr[i] = singleImageData;
	}

	return imageDatasArr;
})(imageDatas);


/*
 * 
 */
function getRangeRandom(low, high){
	return Math.ceil(Math.random() * (high - low) + low);
}

/*
 *  0~30 
 */
function get30DegRandom(){
	return ((Math.random() > 0.5 ? '' : '-') + Math.ceil(Math.random() * 30)); //0.50.5
}

//
class ImageFirgure extends React.Component {
	// handleClick
	constructor(props) {
		super(props);
		this.handleClick = this.handleClick.bind(this);
	}
	/*
	 * imgFigure
	 */
	handleClick(event) {
		//
		if(this.props.arrange.isCenter){
			this.props.inverse();
		}
		//
		else{
			this.props.center();
		}

		// 
		event.stopPropagation();
		event.preventDefault();
	}

	render() {

		let styleObj = {};

		//
		if(this.props.arrange.pos) {
			styleObj = this.props.arrange.pos;
		}

		//0
		if(this.props.arrange.rotate) {
			(['Moz', 'ms', 'Webkit', '']).forEach(function(value)
			{
				styleObj[value + 'Transform'] = 'rotate(' + this.props.arrange.rotate + 'deg)';
			}.bind(this));
		}

		if(this.props.arrange.isCenter){
			styleObj.zIndex = 11;
		}

		//   .img-figure    .img-figure-is-inverse
		let imgFigureClassName = 'img-figure';
		//isInverseimgFigureClassName,
		imgFigureClassName += this.props.arrange.isInverse ? ' is-inverse ' : '';

		return (
			<figure className={imgFigureClassName} style={styleObj} onClick={this.handleClick}>
				<img src={this.props.data.imageURL} alt={this.props.data.title}/>

				<figcaption>
					<h2 className="img-title">{this.props.data.title}</h2>
					<div className="img-back" onClick={this.handleClick}>
						<p>
							{this.props.data.description}
						</p>
					</div>
				</figcaption>
			</figure>
			);
	}
}

//
class ControllerUnit extends React.Component {
	constructor(props) {
		super(props);
		this.handleClick = this.handleClick.bind(this);
	}

	handleClick(event) {
		if(this.props.arrange.isCenter){
			this.props.inverse();
		}
		else{
			this.props.center();
		}


		event.stopPropagation();
		event.preventDefault();
	}

	render() {

		let controllerUnitClassName = 'controller-unit';

		//
		if(this.props.arrange.isCenter){
			controllerUnitClassName += ' is-center';
		}
		if(this.props.arrange.isCenter && this.props.arrange.isInverse){
			controllerUnitClassName += ' is-center' + ' is-inverse';
		}

		return (
			<span className={controllerUnitClassName} onClick={this.handleClick}></span>
		);
	}
}


// AppComponent
class AppComponent extends React.Component {
	//state,constructor,imgsArrangeArr
	constructor(props) {
		
		super(props);
		//
		this.Constant = {
			//
			centerPos: {
				left: 0,
				top: 0
			},
			//
			hPosRange: {
				leftSecX: [0, 0],
				rightSecX: [0, 0],
				y: [0, 0]
			},
			//
			vPosRange: {
				x: [0, 0],
				topY: [0, 0]
			}
		};
		this.state = {
			imgsArrangeArr: [
				/*{
					pos{ //
						left: '0',
						top: '0'
					},
					rotate: 0,   //
					isInverse: false     //false
					isCenter: false     //
				}*/
			]
		};
	}

  	
  	/*
  	 *  
  	 *  @param index inverseindex
  	 *  @return {Function} ,return
  	 */
  	inverse(index){
  		return function(){
  			let imgsArrangeArr = this.state.imgsArrangeArr;

  			imgsArrangeArr[index].isInverse = !imgsArrangeArr[index].isInverse;

  			this.setState({
  				imgsArrangeArr:imgsArrangeArr
  			});
  		}.bind(this);     //bind(this),AppComponent
  	}

  	/*
     * 
  	 */
  	center(index){
  		return function(){
  			this.rearrange(index);
  		}.bind(this);
  	}

	/*
	 *  
	 *  @param centerIndex
	 */
	rearrange(centerIndex) {
		let imgsArrangeArr = this.state.imgsArrangeArr,
			Constant = this.Constant,
			centerPos = Constant.centerPos,
			hPosRange = Constant.hPosRange,
			vPosRange = Constant.vPosRange,
			hposRangeLeftSecX = hPosRange.leftSecX,
			hPosRangeRightSecX = hPosRange.rightSecX,
			hPosRangeY = hPosRange.y,
			vPosRangeX = vPosRange.x,
			vPosRangeTopY = vPosRange.topY,

			//
			imgsArrangeTopArr = [],
			topImgNum = Math.floor(Math.random() * 2), //01
			topImgSpliceIndex = 0,

			imgsArrangeCenterArr = imgsArrangeArr.splice(centerIndex, 1);

			//1 centerIndex ,centerIndex,
			imgsArrangeCenterArr[0] = {
				pos: centerPos,
				rotate: 0,
				isCenter: true
			};

			//2
			// while(topImgSpliceIndex === centerIndex){
			topImgSpliceIndex = Math.floor(Math.random() * (imgsArrangeArr.length - topImgNum));
			// }
			imgsArrangeTopArr = imgsArrangeArr.splice(topImgSpliceIndex, topImgNum);
			//
			imgsArrangeTopArr.forEach( function (value, index) {
				imgsArrangeTopArr[index] = {
					pos:{
						left: getRangeRandom(vPosRangeX[0], vPosRangeX[1]),
						top: getRangeRandom(vPosRangeTopY[0], vPosRangeTopY[1])
					},
					rotate: get30DegRandom(),
					isCenter: false
				};
			});

			//3
			for(var i = 0, j = imgsArrangeArr.length, k = j / 2;i < j; i++){
				//x
				let hPosRangeLORx = null;
				//
				if(i < k){
					hPosRangeLORx = hposRangeLeftSecX;
				}
				else
				{
					hPosRangeLORx = hPosRangeRightSecX;
				}

				imgsArrangeArr[i] = {
					pos: {
						left: getRangeRandom(hPosRangeLORx[0], hPosRangeLORx[1]),
						top: getRangeRandom(hPosRangeY[0], hPosRangeY[1])
					},
					rotate: get30DegRandom(),
					isCenter: false
				};
			}




			//
			if(imgsArrangeTopArr && imgsArrangeTopArr[0]){
				imgsArrangeArr.splice(topImgSpliceIndex, 0, imgsArrangeTopArr[0]);
			}

			//
			imgsArrangeArr.splice(centerIndex, 0, imgsArrangeCenterArr[0]);

			//stateimgsArrangeArr
			this.setState({
				imgsArrangeArr: imgsArrangeArr
			});

			// debugger;
	}

	
	//
	//render
	componentDidMount() {
		//
		let stageDOM = ReactDOM.findDOMNode(this.refs.stage),
			stageW = stageDOM.scrollWidth,
			stageH = stageDOM.scrollHeight,
			halfStageW = Math.ceil(stageW / 2),
			halfStageH = Math.ceil(stageH / 2);

		//imageFigure
		let imgFirgureDOM = ReactDOM.findDOMNode(this.refs.imgFigure0),
			imgW = imgFirgureDOM.scrollWidth,
			imgH = imgFirgureDOM.scrollHeight,
			halfimgW = Math.ceil(imgW / 2),
			halfimgH = Math.ceil(imgH / 2);

		//
		this.Constant.centerPos = {
			left: halfStageW - halfimgW,
			top: halfStageH - halfimgH
		};

		//
		this.Constant.hPosRange.leftSecX[0] = -halfimgW;
		this.Constant.hPosRange.leftSecX[1] = halfStageW - halfimgW * 3;
		this.Constant.hPosRange.rightSecX[0] = halfStageW + halfimgW;
		this.Constant.hPosRange.rightSecX[1] = stageW - halfimgW;
		this.Constant.hPosRange.y[0] = -halfimgH;
		this.Constant.hPosRange.y[1] = stageH - halfimgH;

		//
		this.Constant.vPosRange.x[0] = halfStageW - imgW;
		this.Constant.vPosRange.x[1] = halfStageW;
		this.Constant.vPosRange.topY[0] = -halfimgH;
		this.Constant.vPosRange.topY[1] = halfStageH - halfimgH * 3;

		//,index
		this.rearrange(0);
	}

  render() {

	let controllerUnits = [],
		imgFigures = [];

	imageDatas.forEach( function(value, index){

		//0
		if(!this.state.imgsArrangeArr[index]){
			this.state.imgsArrangeArr[index] = {
				pos: {
					left: 0,
					top: 0
				},
				rotate: 0,
				isInverse: false,
				isCenter: false
			};
		}
		//
		imgFigures.push(<ImageFirgure key={index} data = {value} ref={'imgFigure' + index} arrange={this.state.imgsArrangeArr[index]} inverse={this.inverse(index)} center={this.center(index)}/>);

		controllerUnits.push(<ControllerUnit key={index} arrange={this.state.imgsArrangeArr[index]} inverse={this.inverse(index)} center={this.center(index)}/>);
	}.bind(this));




    return (
      <section className="stage" ref="stage">
      	<section className="img-sec">
      		{imgFigures}
      	</section>
      	<nav className="controller-nav">
      		{controllerUnits}
      	</nav>
      </section>
    );
  }
}

AppComponent.defaultProps = {
};

export default AppComponent;



